@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

.App {
    width: 100%;
    height: 150vh;
    display: flex;
    position: relative;
    background-color:white;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    /* background-image:url(https://images.unsplash.com/photo-1637420704736-4374b91a8c12?q=80&w=2028&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D) */
}

h1 {
    font-family: 'Lobster', cursive;
    font-size: 50px;
    color: #3f9c0a;
}

.draw-area {
    width: 1200;
    height: 80%;
    border: 2px solid #0bb93f;
    position: relative;
    background-color: rgb(193, 164, 164);
    text-align: center;
}

.Menu {
    width: 100%;
    height: 100px;
    display:flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: #250c8b2d;
    margin: auto;
    margin-top: 1px;
    cursor: pointer;
}
.btn
{
    font-size: larger;
    background-color:white;
    color:green;

}